import {
  Application,
  Container,
  Graphics,
  ILoaderOptions,
  IResourceDictionary,
  Loader,
  Text,
  TextStyle
} from "pixi.js"
import "./tools/pixi"
import Game from "./game"
import { Config } from "./config"
import TWEEN from "@tweenjs/tween.js"

const height = 600
const width = 800

export default class Maension {
  private _lastFrame: number = 0
  private _fpsCount: number = 0
  private _fpsTimer: number = 0
  private _game?: Game

  private readonly _container: HTMLDivElement
  private readonly _app: Application
  private readonly _root: Container
  private readonly _loadingBar: Container = new Container()
  private readonly _fps?: Text

  constructor(container: HTMLDivElement) {
    this._container = container

    this._app = new Application({
      width,
      height,
      backgroundColor: Config.constants.black
    })

    this._container.appendChild(this._app.view)

    console.log(
      "Viewport",
      this._container.clientWidth,
      this._container.clientHeight
    )

    // create the root of the scene graph
    // @ts-ignore
    this._root = new Container()

    // @ts-ignore
    this._app.stage.addChild(this._root)

    this.resize()

    if (Config.showFPS) {
      this._fps = new Text("FPS: ", {
        fontWeight: "16px",
        fill: "#fff"
      } as TextStyle)
      this._fps.x = 10
      this._fps.y = 10
      this._app.stage.addChild(this._fps)
    }

    this.drawLoadingBar()

    window.addEventListener(
      "resize",
      this.resize.bind(this)
    )

    requestAnimationFrame(this._tick.bind(this))
  }

  private drawLoadingBar() {
    const outline = new Graphics()
    outline.lineStyle(5, Config.constants.white)

    const lHeight = 50
    const lWidth = 300
    outline.drawRect(
      width / 2 - lWidth / 2,
      height / 2 - lHeight / 2,
      lWidth,
      lHeight
    )

    this._loadingBar.addChild(outline)
    this._app.stage.addChild(this._loadingBar)
  }

  resize() {
    const vpw = window.innerWidth - 50 // Width of the viewport
    const vph = window.innerHeight // Height of the viewport
    let nvw // New game width
    let nvh // New game height

    // The aspect ratio is the ratio of the screen's sizes in different dimensions.
    // The height-to-width aspect ratio of the game is HEIGHT / WIDTH.

    if (vph / vpw < height / width) {
      // If height-to-width ratio of the viewport is less than the height-to-width ratio
      // of the game, then the height will be equal to the height of the viewport, and
      // the width will be scaled.
      nvh = vph
      nvw = (nvh * width) / height
    } else {
      // In the else case, the opposite is happening.
      nvw = vpw
      nvh = (nvw * height) / width
    }

    if (nvw > width) {
      nvw = width
    }

    if (nvh > height) {
      nvh = height
    }

    // Set the game screen size to the new values.
    // This command only makes the screen bigger --- it does not scale the contents of the game.
    // There will be a lot of extra room --- or missing room --- if we don't scale the stage.
    this._app.renderer.resize(nvw, nvh)

    // This command scales the stage to fit the new size of the game.
    this._root.scale.set(nvw / width, nvh / height)
  }

  /**
   *
   */
  public run() {
    const options = {
      crossOrigin: true
    } as ILoaderOptions

    const assetPath = "assets"

    const loader = new Loader()

    // load map
    loader.add(
      "maps.house",
      assetPath + "/maps/house.json",
      options
    )
    loader.add(
      "maps.street",
      assetPath + "/maps/street.json",
      options
    )
    loader.add(
      "maps.maension",
      assetPath + "/maps/maension.json",
      options
    )

    // load player
    loader.add(
      "ryu.stance",
      assetPath + "/ryu/stance.json",
      options
    )
    loader.add(
      "ryu.punch",
      assetPath + "/ryu/punch.json",
      options
    )
    loader.add(
      "ryu.kick",
      assetPath + "/ryu/kick.json",
      options
    )
    loader.add(
      "ryu.walk",
      assetPath + "/ryu/walk.json",
      options
    )
    loader.add(
      "ryu.gothit",
      assetPath + "/ryu/gothit.json",
      options
    )
    loader.add(
      "ryu.died",
      assetPath + "/ryu/died.json",
      options
    )

    loader.add(
      "taro.idle",
      assetPath + "/taro/idle.json",
      options
    )
    loader.add(
      "taro.idle_handgun",
      assetPath + "/taro/idle_handgun.json",
      options
    )

    // enemies
    loader.add(
      "bat.walk",
      assetPath + "/enemys/bat/walk.json",
      options
    )

    // weapons
    loader.add(
      "weapons.shotgun",
      assetPath + "/weapons/shotgun.json",
      options
    )
    loader.add(
      "weapons.beretta",
      assetPath + "/weapons/beretta.json",
      options
    )

    // sounds
    loader.add(
      "bgm.title",
      assetPath + "/sounds/title.m4a",
      options
    )
    loader.add(
      "beretta.fire",
      assetPath +
        "/sounds/beretta/mansion_sfx_gun_berreta_outside.wav",
      options
    )
    loader.add(
      "beretta.reload",
      assetPath +
        "/sounds/beretta/mansion_sfx_gun_berreta_reload.wav",
      options
    )

    loader.add(
      "shaders.frag-smoke",
      assetPath + "/shaders/frag-smoke.glsl",
      options
    )
    loader.add(
      "shaders.frag-smoke2",
      assetPath + "/shaders/frag-smoke2.glsl",
      options
    )

    loader.add(
      "shaders.vert-spot",
      assetPath + "/shaders/vert-spot.glsl",
      options
    )
    loader.add(
      "shaders.frag-spot",
      assetPath + "/shaders/frag-spot.glsl",
      options
    )

    loader.add(
      "shaders.vert-2d",
      assetPath + "/shaders/vert-2d.glsl",
      options
    )
    loader.add(
      "shaders.frag-rain",
      assetPath + "/shaders/frag-rain.glsl",
      options
    )

    loader.add(
      "shaders.vert-light",
      assetPath + "/shaders/vert-light.glsl",
      options
    )
    loader.add(
      "shaders.frag-light",
      assetPath + "/shaders/frag-light.glsl",
      options
    )

    // load things, like decals heads and so on
    loader.add(
      "things.blood",
      assetPath + "/things/blood.json",
      options
    )
    loader.add(
      "things.door",
      assetPath + "/things/door.json",
      options
    )
    loader.add(
      "things.hud",
      assetPath + "/things/hud.json",
      options
    )
    loader.add(
      "things.decals",
      assetPath + "/things/decals.json",
      options
    )
    loader.add(
      "things.muzzle",
      assetPath + "/things/muzzle.json",
      options
    )

    loader.add(
      "lightmap",
      assetPath + "/things/lightmap.png",
      options
    )
    loader.add(
      "flashlight",
      assetPath + "/things/flashlight.png",
      options
    )

    loader.onComplete.once(this._assetsLoaded.bind(this))

    // resources loaded
    loader.load()

    loader.onProgress.add((event: any) => {
      this.updateLoadingBar(event.progress)
    })
  }

  private updateLoadingBar(pct: number) {
    const fill = new Graphics()
    fill.lineStyle(5, Config.constants.white)

    fill.beginFill(Config.constants.white)

    const lHeight = 50
    const lWidth = 300
    fill.drawRect(
      width / 2 - lWidth / 2,
      height / 2 - lHeight / 2,
      (lWidth / 100) * pct,
      lHeight
    )

    this._loadingBar.addChild(fill)
  }

  /**
   *
   * @param loader
   * @param resources
   * @private
   */
  private _assetsLoaded(
    loader: Loader,
    resources: IResourceDictionary
  ) {
    this._app.stage.removeChild(this._loadingBar)

    // loading done, start game
    this._game = new Game(
      resources,
      this._root,
      width,
      height
    )

    console.log("Mänsion:", "Init done, ready to scare!")
  }

  /**
   *
   * @private
   */
  private _tick(elapsed: number) {
    // calculate frame delta
    let delta = elapsed - this._lastFrame
    this._lastFrame = elapsed

    if (this._fps) {
      this._fpsCount++
      this._fpsTimer += delta

      // reset after 1 second
      if (this._fpsTimer > 1000) {
        // update text
        this._fps.text = "FPS: " + this._fpsCount

        this._fpsCount = 0
        this._fpsTimer = 0
      }
    }

    // update game
    if (this._game) {
      this._game.tick(elapsed, delta)
    }

    // render the container
    this._app.render()

    requestAnimationFrame(this._tick.bind(this))

    TWEEN.update(elapsed)
  }
}
