import { AnimationBase } from "./animation-base"
import { IResourceDictionary, Sprite } from "pixi.js"

/**
 *
 */
export class Decale extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    which: string
  ) {
    super(resources)

    const res = this._resources["things.decals"]

    const decale = new Sprite(res.textures![which])

    this.addChild(decale)
  }
}
