import { MathHelper } from "../tools"
import { AnimationBase } from "./animation-base"
import TWEEN from "@tweenjs/tween.js"
import {
  IResourceDictionary,
  Sprite,
  Texture
} from "pixi.js"

/**
 *
 */
export class Shell extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    texture: Texture,
    vx: number,
    vy: number,
    vr: number
  ) {
    super(resources)

    this.pivot.x = this.width / 2
    this.pivot.y = this.height / 2

    this._vr = vr
    this._vx = vx
    this._vy = vy
    const sprite = new Sprite(texture)

    // const t = TweenMax.to(sprite, 2.5, {ease: Back.easeInOut.config(4), y: -200})

    sprite.rotation = MathHelper.getRandomInt(0, 100)

    this.scale.x = 0.05
    this.scale.y = 0.05

    const start = {
      x: 0,
      y: 0
    }

    new TWEEN.Tween(start)
      .to({ x: -MathHelper.getRandomInt(800, 1200) }, 500)
      .repeat(1)
      .onUpdate((coords) => {
        sprite.x = coords.x
      })
      .start(0)

    new TWEEN.Tween(start)
      .to({ y: MathHelper.getRandomInt(1000, 1400) }, 300)
      .easing(TWEEN.Easing.Bounce.Out)
      .repeat(2)
      .yoyo(true)
      .onUpdate((coords) => {
        sprite.y = coords.y
      })
      .start(0)

    this.addChild(sprite)
  }

  tick(delta: number) {}
}
